<script>
var L = require("leaflet");
L.Control.Gdmlayer = require('../modules/leaflet.control.gdmlayer.js')

require('../modules/leaflet.control.opacity.js')
require('leaflet-imageoverlay-rotated')

// import GdmSerieNavigation from './gdm-serie-navigation.vue'

export default {
  name: 'GdmTest',
  
  props: {
    
  },
  data () {
    return {
      map: null,
      controlLayer: null,
      controlOpacity: null,
      imageLayer: null,
      center: [37.0886443,  37.4734276],
      TL: [34.8580475,  39.0314845],
      TR0: [39.3192411,  39.0314845],
      BL0: [34.8580475,  35.9138708],
      TR: null,
      BL: null,
      teta: 0,
      url: 'https://s3.unistra.fr/a2s_TS_EQ/MM_Mean_displacement_EW_WGS84.RGB_4320w.png'
      
    }
  },
  watch: {
    
  },
  created () {
    this.TR = this.TR0
    this.BL = this.BL0
  },
  mounted () {
    console.log('ici')
    this.center = [(this.TR[0] + this.BL[0]) / 2, (this.TR[1] + this.BL[1]) / 2]
    this.initialize()
  },
  destroyed () {
   
  },
  methods: {
   
  
    initialize () {
      var container = this.$el.querySelector('#fmtMap');
      this.map = L.map( container, {scrollWheelZoom: true}).setView([51.505, -0.09], 1);
      this.controlLayer = new L.Control.Gdmlayer(null, null,{position: 'topleft'})
      
      this.controlLayer.tiles.arcgisTopo.layer.addTo(this.map)
      
      this.controlLayer.addTo(this.map)
      // deploy legend by default
    
      this.controlOpacity = new L.Control.Opacity('fr')
      this.controlOpacity.addTo(this.map)
      this.controlOpacity.setVisible(true)
      this.map.fitBounds([[this.TR[1], this.TR[0]], [this.BL[1], this.BL[0]]])
      this.$nextTick(() => {
         this.initImageLayer(-0.001)
      })
     
    },
    
    rotate(pt, teta) {
      // this.imageLayer.remove()
      // this.BL = 
      var result = [
        (pt[0] - this.center[0]) * Math.cos(teta) + (pt[1] - this.center[1]) * Math.sin(teta) + this.center[0],
        (pt[1] - this.center[1]) * Math.cos(teta) - (pt[0] - this.center[0]) * Math.sin(teta) + this.center[1]
      ]
      return result
    },
    initImageLayer (teta) {
      console.log(this.TR)
      console.log(this.TL)
      console.log(this.BL)
      this.TR = this.rotate(this.TR0, teta)
      this.BL = this.rotate(this.BL0, teta)
      this.imageLayer = new L.ImageOverlay.Rotated(this.url, [this.TL[1], this.TL[0]], 
        [this.TR[1], this.TR[0]],
        [this.BL[1], this.BL[0]],
        {opacity: 1}).addTo(this.map)
    },
   
  
  }
}
</script>
